import React from 'react';
import './index.css';
import { ExternalLink } from 'lucide-react';

const Products = () => {
  return (
    <section id="products" className="section">
        <div className="container">
          <h2 className="section-title">Products</h2>
          <div className="projects-grid">
            {[
              {
                title: "EZ-Chess",
                description: "Explainable chess analysis tool using LLMs with MCP-style tooling (Stockfish analysis, Lichess API)",
                image: "data:image/png;base64,",
                tech: ["Python", "Tkinter", "Stockfish", "MCP", "Ollama/Groq"],
                link: "https://pypi.org/project/ez-chess/"
              },
              {
                title: "cybersec-scanner",
                description: "Developed privacy-first Python Library/SDK for efficient and safe software development",
                image: "data:image/png;base64,",
                tech: ["Python", "Git", "Mitmproxy", "Ollama"],
                link: "https://pypi.org/project/cybersec-scanner/"
              },
              
              {title: "AI Trading Coach",
               description: "An AI-powered trading education platform that helps develop smart trading strategies using real-time market analysis and paper trading to help develop market intuition.",
               image: "data:image/jpeg;base64,",
               tech: ["Python", "FastAPI", "AWS", "Docker", "Google OAuth"],
               link: "https://trading.jbac.dev/"
              },
              {title: "YtQGen",
               description: "Open source Python Library/SDK to generate YouTube video transcripts, summaries and Q&A pairs using LLMs.",
               image: "data:image/png;base64,",
               tech: ["Python", "GitHub", "LLM APIs", "PyTest", "PyPi"],
               link: "https://pypi.org/project/ytqgen/"
              },
              {
                title: "Mock Interviewer AI",
                description: "Fully automated mock interview platform that simulates real interviews using AI in great depth, providing instant feedback and interview transcripts to help users improve their skills.",
                image: "data:image/jpeg;base64,",
                tech: ["Streamlit", "FastAPI", "gpt-oss-20b/voxtral-mini-latest", "PostgreSQL", "AWS S3", "Render"],
                link: "https://interview.jbac.dev/"
              },
              {
                title: "Edu QGen",
                description: "Generates concise summaries and structured questionnaires (length by user input) from any uploaded video within 1-2 mins, saves pdf files for future reference.",
                image: "data:image/jpeg;base64,",
                tech: ["React.js", "FastAPI", "gpt-oss-20b/voxtral-mini-latest", "GCP (e2.small)", "AWS S3", "AWS DynamoDB", "Google OAuth"],
                link: "https://questionnaire.jbac.dev/"
              },
              {
                title: "Hackathon Scraper",
                description: "Multi-threaded, automated tool for regularly gathering worldwide hackathons (upto 550+) from premier hackathon websites in <3 mins.",
                image: "data:image/png;base64,",
                tech: ["Selenium", "BeautifulSoup", "Flask", "Multi-threading", "JavaScript", "GitHub Actions"],
                link: "https://hackathons.jbac.dev/"
              }
            ].map((product, index) => (
              <div key={index} className="project-card">
                <img src={product.image} alt={product.title} className="project-image" />
                <div className="project-content">
                  <h3 className="project-title">{product.title}</h3>
                  <p className="project-description">{product.description}</p>
                  <div className="project-tech">
                    {product.tech.map(tech => (
                      <span key={tech} className="tech-tag">
                        {tech}
                      </span>
                    ))}
                  </div>
                  <div className="project-links">
                    <a href={product.link} className="project-link" target="_blank" rel="noopener noreferrer">
                      <ExternalLink size={16} />
                      Try it
                    </a>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>
  );
};

export default Products;